.button.dark {
  --background: #2F3545;
  --shadow: 0 2px 8px -1px rgba(21, 25, 36, 0.32);
  --shadow-hover: 0 4px 20px -2px rgba(21, 25, 36, 0.5);
}

.button {
  --background: #275efe;
  --text: #fff;
  --font-size: 16px;
  --duration: .44s;
  --move-hover: -4px;
  --font-shadow: var(--font-size);
  padding: 16px 32px;
  font-family: 'Roboto';
  font-weight: 500;
  line-height: var(--font-size);
  border-radius: 24px;
  display: block;
  outline: none;
  text-decoration: none;
  font-size: var(--font-size);
  letter-spacing: .5px;
  background: var(--background);
  color: var(--text);
  box-shadow: var(--shadow);
  transform: translateY(var(--y)) translateZ(0);
  transition: transform var(--duration) ease, box-shadow var(--duration) ease;
}

.button div {
  display: flex;
  justify-content: center;
  overflow: hidden;
  text-shadow: 0 var(--font-shadow) 0 var(--text);
}

.button div span {
  color: #fff;
  display: block;
  backface-visibility: hidden;
  font-style: normal;
  transition: transform var(--duration) ease;
  transform: translateY(var(--m)) translateZ(0);
}

.button div span:nth-child(1) {
  transition-delay: 0.05s;
}

.button div span:nth-child(2) {
  transition-delay: 0.1s;
}

.button div span:nth-child(3) {
  transition-delay: 0.15s;
}

.button div span:nth-child(4) {
  transition-delay: 0.2s;
}

.button div span:nth-child(5) {
  transition-delay: 0.25s;
}

.button div span:nth-child(6) {
  transition-delay: 0.3s;
}

.button div span:nth-child(7) {
  transition-delay: 0.35s;
}

.button div span:nth-child(8) {
  transition-delay: 0.4s;
}

.button div span:nth-child(9) {
  transition-delay: 0.45s;
}

.button div span:nth-child(10) {
  transition-delay: 0.5s;
}

.button div span:nth-child(11) {
  transition-delay: 0.55s;
}

.button div span:nth-child(12) {
  transition-delay: 0.6s;
}

.button div span:nth-child(13) {
  transition-delay: 0.65s;
}

.button div span:nth-child(14) {
  transition-delay: 0.7s;
}

.button div span:nth-child(15) {
  transition-delay: 0.75s;
}

.button div span:nth-child(16) {
  transition-delay: 0.8s;
}

.button div span:nth-child(17) {
  transition-delay: 0.85s;
}

.button div span:nth-child(18) {
  transition-delay: 0.9s;
}

.button div span:nth-child(19) {
  transition-delay: 0.95s;
}

.button div span:nth-child(20) {
  transition-delay: 1s;
}

.button div span:nth-child(21) {
  transition-delay: 1.05s;
}

.button div span:nth-child(22) {
  transition-delay: 1.1s;
}

.button div span:nth-child(23) {
  transition-delay: 1.15s;
}

.button div span:nth-child(24) {
  transition-delay: 1.2s;
}

.button:hover {
  --y: var(--move-hover);
  --shadow: var(--shadow-hover);
}

.button:hover span {
  --m: calc(var(--font-size) * -1);
}

.button.reverse {
  --font-shadow: calc(var(--font-size) * -1);
}

.button.reverse:hover span {
  --m: calc(var(--font-size));
}

